@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&family=Rajdhani:wght@300;400;500;600;700&display=swap');

:root {
  /* Neon Gaming Colors */
  --neon-cyan: #00ffff;
  --neon-purple: #8a2be2;
  --neon-pink: #ff1493;
  --neon-green: #39ff14;
  --neon-orange: #ff4500;
  --neon-blue: #0080ff;
  
  /* Gaming Gradients */
  --gaming-gradient: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --neon-gradient: linear-gradient(45deg, var(--neon-cyan), var(--neon-purple));
  --cyber-gradient: linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 50%, #16213e 100%);
  --holographic: linear-gradient(45deg, #ff0080, #ff8c00, #40e0d0, #ff0080);
  
  /* Glass & Transparency */
  --glass-bg: rgba(15, 15, 35, 0.8);
  --glass-border: rgba(255, 255, 255, 0.1);
  --glass-glow: rgba(0, 255, 255, 0.3);
  
  /* Text Colors */
  --text-neon: #00ffff;
  --text-primary: #ffffff;
  --text-secondary: rgba(255, 255, 255, 0.8);
  --text-muted: rgba(255, 255, 255, 0.6);
  
  /* Shadows & Effects */
  --neon-shadow: 0 0 20px var(--neon-cyan), 0 0 40px var(--neon-cyan), 0 0 60px var(--neon-cyan);
  --cyber-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  --hologram-shadow: 0 0 30px rgba(64, 224, 208, 0.5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Rajdhani', sans-serif;
  background: var(--cyber-gradient);
  overflow: hidden;
  position: relative;
}

/* Animated Background */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(0, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 20, 147, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(138, 43, 226, 0.1) 0%, transparent 50%);
  z-index: -2;
  animation: backgroundPulse 8s ease-in-out infinite alternate;
}

@keyframes backgroundPulse {
  0% { opacity: 0.5; }
  100% { opacity: 1; }
}

/* Cyber Grid Background */
body::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 255, 0.03) 1px, transparent 1px);
  background-size: 50px 50px;
  z-index: -1;
  animation: gridMove 20s linear infinite;
}

@keyframes gridMove {
  0% { transform: translate(0, 0); }
  100% { transform: translate(50px, 50px); }
}

/* Login Page Styles */
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}

.login-card {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 2px solid var(--glass-border);
  border-radius: 25px;
  padding: 40px;
  width: 450px;
  box-shadow: var(--cyber-shadow), inset 0 1px 0 rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
  animation: loginSlideIn 1s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.login-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 255, 255, 0.1), transparent);
  animation: shimmer 3s infinite;
}

@keyframes loginSlideIn {
  0% { 
    opacity: 0;
    transform: translateY(100px) scale(0.8) rotateX(30deg);
  }
  100% { 
    opacity: 1;
    transform: translateY(0) scale(1) rotateX(0deg);
  }
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.login-header {
  text-align: center;
  margin-bottom: 30px;
}

.login-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  display: inline-block;
  animation: iconFloat 3s ease-in-out infinite;
  filter: drop-shadow(0 0 20px var(--neon-cyan));
}

@keyframes iconFloat {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(-10px) rotate(5deg); }
}

.login-header h1 {
  font-family: 'Orbitron', monospace;
  font-size: 2.5rem;
  font-weight: 900;
  color: var(--text-neon);
  text-shadow: var(--neon-shadow);
  margin-bottom: 10px;
  animation: textGlow 2s ease-in-out infinite alternate;
}

@keyframes textGlow {
  0% { text-shadow: 0 0 20px var(--neon-cyan); }
  100% { text-shadow: var(--neon-shadow); }
}

.login-header p {
  color: var(--text-secondary);
  font-size: 1.1rem;
  font-weight: 500;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.login-input {
  background: rgba(15, 15, 35, 0.6);
  border: 2px solid var(--glass-border);
  border-radius: 15px;
  padding: 15px 20px;
  color: var(--text-primary);
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.login-input:focus {
  outline: none;
  border-color: var(--neon-cyan);
  box-shadow: 0 0 25px rgba(0, 255, 255, 0.3);
  background: rgba(15, 15, 35, 0.8);
  transform: translateY(-2px);
}

.login-input::placeholder {
  color: var(--text-muted);
}

.login-button, .toggle-button {
  background: var(--neon-gradient);
  border: none;
  border-radius: 15px;
  padding: 15px 25px;
  color: white;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.login-button::before, .toggle-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s;
}

.login-button:hover::before, .toggle-button:hover::before {
  left: 100%;
}

.login-button:hover, .toggle-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(0, 255, 255, 0.4);
}

.login-button:active, .toggle-button:active {
  transform: translateY(-1px);
}

.toggle-button {
  background: transparent;
  border: 2px solid var(--glass-border);
  color: var(--text-secondary);
}

.toggle-button:hover {
  border-color: var(--neon-cyan);
  color: var(--text-neon);
}

.error-message {
  color: #ff4757;
  text-align: center;
  font-weight: 600;
  animation: errorShake 0.5s ease-in-out;
}

@keyframes errorShake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}

.background-shapes {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.shape {
  position: absolute;
  border-radius: 50%;
  background: var(--holographic);
  opacity: 0.1;
  animation: shapeFloat 15s ease-in-out infinite;
}

.shape-1 {
  width: 300px;
  height: 300px;
  top: 10%;
  left: 10%;
  animation-delay: 0s;
}

.shape-2 {
  width: 200px;
  height: 200px;
  bottom: 20%;
  right: 15%;
  animation-delay: -7s;
}

@keyframes shapeFloat {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  33% { transform: translateY(-30px) rotate(120deg); }
  66% { transform: translateY(20px) rotate(240deg); }
}

/* Main App Container */
.app-container {
  display: flex;
  height: 100vh;
  animation: appLoadIn 1s ease-out;
}

@keyframes appLoadIn {
  0% { 
    opacity: 0;
    transform: scale(0.95);
  }
  100% { 
    opacity: 1;
    transform: scale(1);
  }
}

/* Sidebar */
.sidebar {
  width: 80px;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border-right: 2px solid var(--glass-border);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  gap: 15px;
  position: relative;
  overflow: hidden;
}

.sidebar::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, 
    rgba(0, 255, 255, 0.05) 0%,
    transparent 50%,
    rgba(255, 20, 147, 0.05) 100%
  );
  z-index: -1;
}

.app-logo {
  width: 55px;
  height: 55px;
  border-radius: 18px;
  background: var(--neon-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  color: white;
  font-weight: bold;
  margin-bottom: 20px;
  box-shadow: var(--cyber-shadow);
  animation: logoRotate 4s ease-in-out infinite;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.app-logo:hover {
  transform: scale(1.1) rotate(10deg);
  box-shadow: 0 0 30px var(--neon-cyan);
}

@keyframes logoRotate {
  0%, 100% { transform: rotate(0deg) scale(1); }
  25% { transform: rotate(5deg) scale(1.05); }
  50% { transform: rotate(0deg) scale(1); }
  75% { transform: rotate(-5deg) scale(1.05); }
}

.channel-button, .stream-button, .logout-button {
  width: 55px;
  height: 55px;
  border: 2px solid var(--glass-border);
  border-radius: 18px;
  background: var(--glass-bg);
  backdrop-filter: blur(10px);
  color: var(--text-secondary);
  font-size: 1.4rem;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.channel-button::before, .stream-button::before, .logout-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(0, 255, 255, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.3s;
}

.channel-button:hover::before, .stream-button:hover::before, .logout-button:hover::before {
  opacity: 1;
}

.channel-button:hover, .stream-button:hover, .logout-button:hover {
  transform: translateY(-5px) scale(1.1);
  border-color: var(--neon-cyan);
  color: var(--neon-cyan);
  box-shadow: 0 10px 25px rgba(0, 255, 255, 0.3);
}

.channel-button.active, .stream-button.active {
  background: var(--neon-gradient);
  color: white;
  border-color: transparent;
  box-shadow: var(--neon-shadow);
  animation: activeGlow 2s ease-in-out infinite alternate;
}

@keyframes activeGlow {
  0% { box-shadow: 0 0 20px var(--neon-cyan); }
  100% { box-shadow: var(--neon-shadow); }
}

.logout-button:hover {
  border-color: #ff4757;
  color: #ff4757;
  box-shadow: 0 10px 25px rgba(255, 71, 87, 0.3);
}

/* Main Content */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: rgba(15, 15, 35, 0.4);
  backdrop-filter: blur(15px);
  position: relative;
  overflow: hidden;
}

.main-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 30% 40%, rgba(0, 255, 255, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 70% 60%, rgba(255, 20, 147, 0.05) 0%, transparent 50%);
  z-index: -1;
}

/* Chat Header */
.chat-header {
  height: 70px;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border-bottom: 2px solid var(--glass-border);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px;
  animation: headerSlide 0.8s ease-out;
}

@keyframes headerSlide {
  0% { 
    opacity: 0;
    transform: translateY(-100%);
  }
  100% { 
    opacity: 1;
    transform: translateY(0);
  }
}

.channel-title {
  font-family: 'Orbitron', monospace;
  color: var(--text-neon);
  font-size: 1.4rem;
  font-weight: 700;
  text-shadow: 0 0 10px var(--neon-cyan);
  display: flex;
  align-items: center;
  gap: 12px;
  animation: titleGlow 3s ease-in-out infinite alternate;
}

@keyframes titleGlow {
  0% { text-shadow: 0 0 10px var(--neon-cyan); }
  100% { text-shadow: 0 0 20px var(--neon-cyan), 0 0 30px var(--neon-cyan); }
}

.current-user-display {
  color: var(--text-secondary);
  font-weight: 600;
  background: rgba(0, 255, 255, 0.1);
  padding: 8px 16px;
  border-radius: 20px;
  border: 1px solid var(--glass-border);
  backdrop-filter: blur(10px);
}

/* Voice Controls */
.voice-controls {
  display: flex;
  align-items: center;
  gap: 15px;
}

.connection-status {
  color: var(--neon-green);
  font-size: 0.9rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
}

.connection-status::before {
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--neon-green);
  animation: statusPulse 2s infinite;
}

@keyframes statusPulse {
  0%, 100% { 
    opacity: 1;
    transform: scale(1);
    box-shadow: 0 0 10px var(--neon-green);
  }
  50% { 
    opacity: 0.5;
    transform: scale(1.3);
    box-shadow: 0 0 20px var(--neon-green);
  }
}

.mute-button, .voice-toggle-button {
  background: var(--glass-bg);
  border: 2px solid var(--glass-border);
  border-radius: 12px;
  padding: 10px 16px;
  color: var(--text-primary);
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 600;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
}

.mute-button:hover, .voice-toggle-button:hover {
  transform: translateY(-2px);
  border-color: var(--neon-cyan);
  box-shadow: 0 8px 20px rgba(0, 255, 255, 0.3);
  color: var(--neon-cyan);
}

.mute-button:disabled, .voice-toggle-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Chat Messages Area */
.chat-messages-area {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: var(--neon-cyan) transparent;
}

.chat-messages-area::-webkit-scrollbar {
  width: 8px;
}

.chat-messages-area::-webkit-scrollbar-track {
  background: transparent;
}

.chat-messages-area::-webkit-scrollbar-thumb {
  background: linear-gradient(var(--neon-cyan), var(--neon-purple));
  border-radius: 10px;
}

.message-item {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  animation: messageSlideIn 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

@keyframes messageSlideIn {
  0% { 
    opacity: 0;
    transform: translateX(-50px) scale(0.8);
  }
  100% { 
    opacity: 1;
    transform: translateX(0) scale(1);
  }
}

.message-avatar {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: var(--neon-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 1.1rem;
  flex-shrink: 0;
  box-shadow: 0 5px 15px rgba(0, 255, 255, 0.3);
  animation: avatarGlow 3s ease-in-out infinite alternate;
}

@keyframes avatarGlow {
  0% { box-shadow: 0 5px 15px rgba(0, 255, 255, 0.3); }
  100% { box-shadow: 0 5px 15px rgba(255, 20, 147, 0.3); }
}

.message-content-wrapper {
  flex: 1;
  min-width: 0;
}

.message-meta {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 5px;
}

.message-author {
  color: var(--text-neon);
  font-weight: 700;
  font-size: 1rem;
}

.message-time {
  color: var(--text-muted);
  font-size: 0.8rem;
}

.message-text {
  color: var(--text-primary);
  font-size: 1rem;
  line-height: 1.4;
  background: rgba(255, 255, 255, 0.05);
  padding: 10px 15px;
  border-radius: 15px;
  border-left: 3px solid var(--neon-cyan);
  backdrop-filter: blur(10px);
}

/* Voice Grid */
.voice-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 25px;
  animation: gridFadeIn 1s ease-out;
}

@keyframes gridFadeIn {
  0% { 
    opacity: 0;
    transform: translateY(30px);
  }
  100% { 
    opacity: 1;
    transform: translateY(0);
  }
}

/* Voice Cards */
.voice-card {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 2px solid var(--glass-border);
  border-radius: 20px;
  padding: 25px;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  animation: cardSlideUp 0.6s ease-out forwards;
  opacity: 0;
}

.voice-card:nth-child(1) { animation-delay: 0.1s; }
.voice-card:nth-child(2) { animation-delay: 0.2s; }
.voice-card:nth-child(3) { animation-delay: 0.3s; }
.voice-card:nth-child(4) { animation-delay: 0.4s; }
.voice-card:nth-child(5) { animation-delay: 0.5s; }
.voice-card:nth-child(6) { animation-delay: 0.6s; }

@keyframes cardSlideUp {
  0% { 
    opacity: 0;
    transform: translateY(50px) rotateX(30deg);
  }
  100% { 
    opacity: 1;
    transform: translateY(0) rotateX(0deg);
  }
}

.voice-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, 
    rgba(0, 255, 255, 0.05) 0%,
    rgba(255, 20, 147, 0.05) 100%
  );
  opacity: 0;
  transition: opacity 0.3s;
  z-index: -1;
}

.voice-card:hover::before {
  opacity: 1;
}

.voice-card:hover {
  transform: translateY(-15px) scale(1.02);
  border-color: var(--neon-cyan);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.3),
    0 0 30px rgba(0, 255, 255, 0.3);
}

.voice-card.speaking {
  border: 3px solid var(--neon-green) !important;
  box-shadow: 
    0 0 0 2px var(--neon-green),
    0 0 30px rgba(57, 255, 20, 0.7),
    inset 0 0 20px rgba(57, 255, 20, 0.3) !important;
  animation: speaking-pulse 1.5s ease-in-out infinite alternate;
}

@keyframes speaking-pulse {
  from {
    box-shadow: 
      0 0 0 2px var(--neon-green),
      0 0 30px rgba(57, 255, 20, 0.7),
      inset 0 0 20px rgba(57, 255, 20, 0.3);
  }
  to {
    box-shadow: 
      0 0 0 4px var(--neon-green),
      0 0 50px rgba(57, 255, 20, 0.9),
      inset 0 0 25px rgba(57, 255, 20, 0.4);
  }
}

.voice-card.streaming {
  border-color: var(--neon-purple);
  box-shadow: 0 0 40px rgba(138, 43, 226, 0.5);
}

.voice-card.streaming::after {
  content: 'ðŸ“º';
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.2rem;
  animation: streamingIcon 2s ease-in-out infinite;
}

@keyframes streamingIcon {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.2); }
}

/* Stream Preview */
.stream-preview {
  position: relative;
  margin-bottom: 15px;
  border-radius: 15px;
  overflow: hidden;
  background: #000;
}

.stream-video {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 15px;
}

.stream-controls {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  gap: 5px;
  opacity: 0;
  transition: opacity 0.3s;
}

.stream-preview:hover .stream-controls {
  opacity: 1;
}

.stream-control-btn {
  background: rgba(0, 0, 0, 0.7);
  border: none;
  border-radius: 8px;
  color: white;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s;
  backdrop-filter: blur(10px);
}

.stream-control-btn:hover {
  background: rgba(0, 255, 255, 0.3);
  transform: scale(1.1);
}

/* Voice Avatar */
.voice-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: var(--neon-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  font-weight: bold;
  margin: 0 auto 20px;
  position: relative;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
}

.voice-avatar::after {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  border-radius: 50%;
  background: conic-gradient(var(--neon-cyan), var(--neon-purple), var(--neon-pink), var(--neon-cyan));
  animation: avatarRotate 4s linear infinite;
  opacity: 0;
  transition: opacity 0.3s;
  z-index: -1;
}

.voice-card:hover .voice-avatar::after {
  opacity: 0.7;
}

@keyframes avatarRotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.voice-card:hover .voice-avatar {
  transform: scale(1.1);
  box-shadow: 0 15px 35px rgba(0, 255, 255, 0.4);
}

.voice-name {
  font-size: 1.3rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 10px;
  transition: all 0.3s;
}

.voice-card:hover .voice-name {
  color: var(--neon-cyan);
  transform: scale(1.05);
  text-shadow: 0 0 10px var(--neon-cyan);
}

.voice-status {
  font-size: 1rem;
  color: var(--text-secondary);
  margin-bottom: 15px;
  opacity: 0.8;
  transition: all 0.3s;
  font-weight: 500;
}

.voice-card:hover .voice-status {
  opacity: 1;
  color: var(--text-primary);
}

/* Volume Control */
.volume-control {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 15px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  backdrop-filter: blur(10px);
}

.volume-slider {
  flex: 1;
  height: 6px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  outline: none;
  appearance: none;
}

.volume-slider::-webkit-slider-thumb {
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--neon-gradient);
  cursor: pointer;
  box-shadow: 0 0 10px var(--neon-cyan);
}

.volume-slider::-moz-range-thumb {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--neon-gradient);
  cursor: pointer;
  border: none;
  box-shadow: 0 0 10px var(--neon-cyan);
}

.volume-value {
  color: var(--neon-cyan);
  font-weight: 600;
  font-size: 0.9rem;
  min-width: 40px;
}

/* Message Input Area */
.message-input-area {
  padding: 20px;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border-top: 2px solid var(--glass-border);
}

.message-input-group {
  display: flex;
  gap: 15px;
  align-items: center;
}

.message-input {
  flex: 1;
  background: rgba(15, 15, 35, 0.6);
  border: 2px solid var(--glass-border);
  border-radius: 25px;
  padding: 15px 20px;
  color: var(--text-primary);
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
}

.message-input:focus {
  outline: none;
  border-color: var(--neon-cyan);
  box-shadow: 0 0 25px rgba(0, 255, 255, 0.3);
  background: rgba(15, 15, 35, 0.8);
}

.message-input::placeholder {
  color: var(--text-muted);
}

.send-button {
  background: var(--neon-gradient);
  border: none;
  border-radius: 25px;
  padding: 15px 25px;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.send-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s;
}

.send-button:hover::before {
  left: 100%;
}

.send-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 255, 0.4);
}

/* Online Sidebar */
.online-sidebar {
  width: 280px;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border-left: 2px solid var(--glass-border);
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  animation: sidebarSlideIn 0.8s ease-out;
}

@keyframes sidebarSlideIn {
  0% { 
    opacity: 0;
    transform: translateX(100%);
  }
  100% { 
    opacity: 1;
    transform: translateX(0);
  }
}

.online-sidebar::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(270deg, 
    rgba(0, 255, 255, 0.03) 0%,
    transparent 50%,
    rgba(255, 20, 147, 0.03) 100%
  );
  z-index: -1;
}

.online-header {
  padding: 25px 20px;
  border-bottom: 2px solid var(--glass-border);
  background: rgba(255, 255, 255, 0.05);
}

.online-header h3 {
  color: var(--text-neon);
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: 'Orbitron', monospace;
  text-shadow: 0 0 10px var(--neon-cyan);
}

.connection-info {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
}

.status-indicator {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  animation: statusPulse 2s infinite;
}

.status-indicator.connected {
  background: var(--neon-green);
}

.status-indicator.connecting {
  background: var(--neon-orange);
  animation: connectingBlink 1s infinite;
}

@keyframes connectingBlink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0.3; }
}

.status-indicator.disconnected {
  background: #ff4757;
}

.status-indicator.error {
  background: #ff4757;
  animation: errorFlash 0.5s infinite;
}

@keyframes errorFlash {
  0%, 100% { background: #ff4757; }
  50% { background: #ff6b7a; }
}

.status-text {
  color: var(--text-secondary);
  font-size: 0.9rem;
  font-weight: 500;
}

.online-list {
  flex: 1;
  overflow-y: auto;
  padding: 15px 0;
  scrollbar-width: thin;
  scrollbar-color: var(--neon-cyan) transparent;
}

.online-list::-webkit-scrollbar {
  width: 6px;
}

.online-list::-webkit-scrollbar-thumb {
  background: linear-gradient(var(--neon-cyan), var(--neon-purple));
  border-radius: 10px;
}

.online-user-item {
  display: flex;
  align-items: center;
  padding: 15px 20px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.online-user-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(0, 255, 255, 0.1), 
    transparent
  );
  transition: left 0.6s;
}

.online-user-item:hover::before {
  left: 100%;
}

.online-user-item:hover {
  background: rgba(0, 255, 255, 0.1);
  border-left: 3px solid var(--neon-cyan);
  transform: translateX(5px);
  box-shadow: inset 0 0 20px rgba(0, 255, 255, 0.1);
}

.user-status-indicator {
  position: absolute;
  bottom: -2px;
  right: -2px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 2px solid var(--glass-bg);
  animation: statusPulse 3s infinite;
}

.user-status-indicator.online {
  background: var(--neon-green);
}

.user-status-indicator.speaking {
  background: var(--neon-cyan);
  animation: speakingRipple 1s infinite;
}

@keyframes speakingRipple {
  0% { 
    transform: scale(1);
    box-shadow: 0 0 10px var(--neon-cyan);
  }
  50% { 
    transform: scale(1.3);
    box-shadow: 0 0 20px var(--neon-cyan);
  }
  100% { 
    transform: scale(1);
    box-shadow: 0 0 10px var(--neon-cyan);
  }
}

.user-avatar {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: var(--neon-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 1.1rem;
  margin-right: 15px;
  position: relative;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  transition: all 0.3s;
}

.online-user-item:hover .user-avatar {
  transform: scale(1.1);
  box-shadow: 0 8px 20px rgba(0, 255, 255, 0.4);
}

.user-info {
  flex: 1;
  min-width: 0;
}

.user-name {
  color: var(--text-primary);
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 3px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: color 0.3s;
}

.online-user-item:hover .user-name {
  color: var(--neon-cyan);
}

.user-game {
  color: var(--text-muted);
  font-size: 0.85rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 500;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: modalFadeIn 0.3s ease-out;
}

@keyframes modalFadeIn {
  0% { 
    opacity: 0;
    backdrop-filter: blur(0px);
  }
  100% { 
    opacity: 1;
    backdrop-filter: blur(10px);
  }
}

.modal-content {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 2px solid var(--glass-border);
  border-radius: 20px;
  padding: 40px;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  animation: modalSlideIn 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

@keyframes modalSlideIn {
  0% { 
    opacity: 0;
    transform: scale(0.8) translateY(50px);
  }
  100% { 
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.modal-content h2 {
  color: var(--text-neon);
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 25px;
  text-align: center;
  font-family: 'Orbitron', monospace;
  text-shadow: 0 0 15px var(--neon-cyan);
}

.quality-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 25px;
}

.quality-option {
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid var(--glass-border);
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.quality-option:hover {
  border-color: var(--neon-cyan);
  background: rgba(0, 255, 255, 0.1);
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 255, 255, 0.2);
}

.quality-option h3 {
  color: var(--text-primary);
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 15px;
}

.fps-options {
  display: flex;
  gap: 10px;
  justify-content: center;
}

.fps-button {
  background: var(--glass-bg);
  border: 2px solid var(--glass-border);
  border-radius: 10px;
  padding: 8px 16px;
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.3s;
  font-weight: 600;
}

.fps-button:hover {
  border-color: var(--neon-purple);
  background: rgba(138, 43, 226, 0.2);
  color: var(--neon-purple);
  transform: scale(1.05);
}

.source-options {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 25px;
}

.source-button {
  background: var(--glass-bg);
  border: 2px solid var(--glass-border);
  border-radius: 15px;
  padding: 20px;
  color: var(--text-primary);
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

.source-button:hover {
  border-color: var(--neon-cyan);
  background: rgba(0, 255, 255, 0.1);
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0, 255, 255, 0.3);
}

.quality-info {
  color: var(--text-secondary);
  font-size: 1rem;
  text-align: center;
  margin-bottom: 20px;
  padding: 15px;
  background: rgba(0, 255, 255, 0.1);
  border-radius: 10px;
  border: 1px solid var(--glass-border);
}

.modal-close {
  background: transparent;
  border: 2px solid #ff4757;
  border-radius: 10px;
  padding: 12px 25px;
  color: #ff4757;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s;
  display: block;
  margin: 0 auto;
}

.modal-close:hover {
  background: rgba(255, 71, 87, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(255, 71, 87, 0.3);
}

/* Fullscreen Stream */
.fullscreen-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  animation: fullscreenFadeIn 0.3s ease-out;
}

@keyframes fullscreenFadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

.fullscreen-video {
  max-width: 95%;
  max-height: 95%;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  animation: fullscreenZoomIn 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

@keyframes fullscreenZoomIn {
  0% { 
    opacity: 0;
    transform: scale(0.5);
  }
  100% { 
    opacity: 1;
    transform: scale(1);
  }
}

.fullscreen-info {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 2px solid var(--glass-border);
  border-radius: 15px;
  padding: 15px 25px;
  color: var(--text-neon);
  font-weight: 600;
  font-size: 1.1rem;
}

.fullscreen-close {
  position: absolute;
  top: 30px;
  right: 30px;
  background: rgba(255, 71, 87, 0.8);
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.3s;
  backdrop-filter: blur(10px);
}

.fullscreen-close:hover {
  background: rgba(255, 71, 87, 1);
  transform: scale(1.1);
  box-shadow: 0 5px 20px rgba(255, 71, 87, 0.4);
}

/* Mini Windows */
.mini-window {
  position: fixed;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 2px solid var(--glass-border);
  border-radius: 15px;
  overflow: hidden;
  z-index: 1500;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  animation: miniWindowSlideIn 0.3s ease-out;
  resize: both;
  min-width: 280px;
  min-height: 180px;
}

@keyframes miniWindowSlideIn {
  0% { 
    opacity: 0;
    transform: scale(0.8);
  }
  100% { 
    opacity: 1;
    transform: scale(1);
  }
}

.mini-window-header {
  background: rgba(0, 0, 0, 0.5);
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: move;
  color: var(--text-primary);
  font-weight: 600;
}

.mini-window-header button {
  background: rgba(255, 71, 87, 0.8);
  border: none;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  color: white;
  cursor: pointer;
  transition: all 0.3s;
}

.mini-window-header button:hover {
  background: rgba(255, 71, 87, 1);
  transform: scale(1.1);
}

.mini-video {
  width: 100%;
  height: calc(100% - 45px);
  object-fit: cover;
}

/* Utility Classes */
.animate-fade-in {
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

/* Responsive Design */
@media (max-width: 1200px) {
  .voice-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 20px;
  }
  
  .online-sidebar {
    width: 250px;
  }
}

@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
    height: 100vh;
  }
  
  .sidebar {
    width: 100%;
    height: 70px;
    flex-direction: row;
    justify-content: space-around;
    padding: 10px;
    order: 3;
  }
  
  .main-content {
    flex: 1;
    order: 1;
  }
  
  .online-sidebar {
    width: 100%;
    height: 200px;
    order: 2;
    border-left: none;
    border-top: 2px solid var(--glass-border);
  }
  
  .voice-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 15px;
    padding: 15px;
  }
  
  .chat-header {
    flex-wrap: wrap;
    height: auto;
    min-height: 70px;
    padding: 15px 20px;
  }
  
  .voice-controls {
    width: 100%;
    justify-content: center;
    margin-top: 10px;
  }
  
  .modal-content {
    width: 95%;
    padding: 25px;
  }
  
  .quality-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .sidebar {
    height: 60px;
    gap: 10px;
  }
  
  .app-logo, .channel-button, .stream-button, .logout-button {
    width: 45px;
    height: 45px;
    font-size: 1.2rem;
  }
  
  .voice-grid {
    grid-template-columns: 1fr;
    padding: 10px;
  }
  
  .voice-card {
    padding: 20px;
  }
  
  .voice-avatar {
    width: 70px;
    height: 70px;
    font-size: 1.8rem;
  }
  
  .login-card {
    width: 90%;
    padding: 30px;
  }
}